.cat-image-container {
  text-align: center;
  margin: 20px;
}

.cat-image-container img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  border-radius: 10px;
}

.cat-image-container button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
}

.cat-image-container button:hover {
  background-color: black;
  color: white;
}

.App {
  text-align: center;
}
